var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"item",attrs:{"id":"backdrop"}},[_c('div',{staticClass:"line"}),_c('div',{staticClass:"title"},[_vm._v("实验背景")]),_c('div',{staticClass:"content"},[_c('div',{staticClass:"main-text"},[_c('p',{staticClass:"info-title"},[_vm._v("摘要")]),_c('div',{staticClass:"info-text"},[_c('p',[_vm._v(" 我们日常生活中所使用的能源主要以天然气等不可再生能源为主，这类能源的使用是不利于可持续发展目标的。对此，进行可再生能源的开发工作，是当前世界范围内所关注的重点。风能是一种常见的可再生能源，其开发的潜力巨大。 ")]),_c('p',[_vm._v(" 风能虽然仅仅是太阳能的一种转换形式，并且在太阳能中一般只有2%左右的能量可以转化成为风能，但如果从总量上来看，还是十分可观的。风能可以算作如今最有商业前景的一种可再生能源。在这一基础上，风力发电的相关技术也在不断发展与进步。 ")]),_c('p',[_vm._v(" 风力发电主要是利用风动能来进行转化，使其成为机械动能，最后再把机械能转换成为电能。风能本身是一种可再生能源，因此，风力发电本身就具有一定的环保性质。2005年《可再生能源法》立法后，风能这类可再生能源得到了更多的重视，其意义更是不容忽视的。在这一背景下，大力发展可再生能源是一种必然趋势。以我国为例，其风力发电最初出现在20世纪80年代中期，经过长时间的发展，我国的风力发电市场已经有了十分显著的进步。风力发电的使用不仅仅具有一定的环保优势，同时这种能源也具有可再生的优点。不过，在当前，风力发电技术也还有一些不足。基于此，相关从业人员还需要积极提升风力发电的稳定性，使我国风力发电的技术能够不断发展与进步。 ")])]),_c('p',{staticClass:"info-title"},[_vm._v("风力发电现状")]),_c('div',{staticClass:"info-text"},[_c('p',[_vm._v(" 风力发电主要是利用风动能来进行转化，使其成为机械动能，最后再把机械能转换成为电能。风能本身是一种可再生能源，因此，风力发电本身就具有一定的环保性质。2005年《可再生能源法》立法后，风能这类可再生能源得到了更多的重视，其意义更是不容忽视的。在这一背景下，大力发展可再生能源是一种必然趋势。以我国为例，其风力发电最初出现在20世纪80年代中期，经过长时间的发展，我国的风力发电市场已经有了十分显著的进步。风力发电的使用不仅仅具有一定的环保优势，同时这种能源也具有可再生的优点。不过，在当前，风力发电技术也还有一些不足。基于此，相关从业人员还需要积极提升风力发电的稳定性，使我国风力发电的技术能够不断发展与进步。 ")]),_c('p',[_vm._v(" 风力发电具有诸多优势，能够有效降低环境污染及能源成本，同时风力发电技术也相对成熟，因此目前风电行业发展速度较快，全球风力发电机发电容量正在以每年20%以上的速度递增。截止到2020年，全球风机总装机容量为743 GW，其中中国风电装机容量世界第一，为281 GW，是排名第二的美国风电装机容量的130%，并高于欧盟总和。由于我国对风电事业的持续性投入，使得风力发电技术不断发展成熟，风电行业未来的发展前景相当可观。 ")])]),_c('p',{staticClass:"info-title"},[_vm._v("风力发电优点")]),_c('div',{staticClass:"info-text"},[_c('p',[_vm._v(" 风力发电技术在实际应用中存在很多优点，主要表现为以下方面。（1）经济效益好。在进行风力发电的过程中，风力发电的价格不高，随着技术成熟，价格下降速度较快，有的风力发电的成本已经接近煤炭的发电成本。除此之外，我国的风力能源较为丰富，在日后发展的过程中，经济效益会更加突出。（2）建设工期较短，建设完成后见效较快。由于风电工程的建设速度很快，在短期之内就可以完成工程项目的建设，从而可以有效解决一些急用的情况。风力发电技术的应用在一些偏远地区有重要作用，通过合理运用该技术，能够更好地满足偏远区域人们的用电需要。 ")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"item",attrs:{"id":"guide"}},[_c('div',{staticClass:"line"}),_c('div',{staticClass:"title"},[_vm._v("实验指南")]),_c('div',{staticClass:"content"},[_c('div',{staticClass:"main-text"},[_c('p',{staticClass:"info-title"},[_vm._v("风力发电基本原理")]),_c('div',{staticClass:"info-text"},[_c('p',[_vm._v(" 风能的原理是将风能转化为机械能，然后转化为机械能和输出能。具体技术是风使风力叶片转动，使发电机内部旋转并切割磁场，最后能量积累装置以电能的形式保持恒定的电流输出。风力发电机通常由风力发电机叶片、低速轴、高速轴、风速计、塔楼、发电机、液压系统组成。其中，风轮是一种将风能转化为机器的装置，可根据风向的变化改变风轮的方向，从而最大限度地利用风能。塔是连接和支撑风轮和发电机的支座，其高度取决于周围地形和风轮的大小，以确保风轮的正常运行。发电机是一种将机械能从风轮转换为电能的装置。 ")]),_c('p',[_vm._v(" 在风力发电机结构中，风力发电叶尖的线性速度与风速的关系被定义为顶点速度比，这是风力发电机的重要参数，其尺寸是影响风力发电机功率系数的重要参数。风力涡轮机中的电力调节是风力发电系统的关键技术工具。主要包括距离失速控制，失速控制，支架可以调整固定间距，叶片倾斜角不能按风速调整，设计比较简单可靠，风机输出功率随着风的速度而变化，在低风速下使用系数较低，螺杆调整的目的是改变倾斜角，调节风能转换效率，最大化能量转换效率，已达到风力发电机电力稳定输出功率的目的。 ")])]),_c('p',{staticClass:"info-title"},[_vm._v("认识风力发电设备")]),_c('div',{staticClass:"info-text"},[_c('p',[_vm._v(" 机舱：机舱包容着风力发电机的关键设备，包括齿轮箱、发电机。维护人员可以通过风力发电机塔进入机舱。机舱左端是风力发电机转子，即转子叶片及轴。 ")]),_c('p',[_vm._v(" 转子叶片：捉获风，并将风力传送到转子轴心。现代600千瓦风力发电机上，每个转子叶片的测量长度大约为20米，而且被设计得很像飞机的机翼。 ")]),_c('p',[_vm._v("轴心：转子轴心附着在风力发电机的低速轴上。")]),_c('p',[_vm._v(" 低速轴：风力发电机的低速轴将转子轴心与齿轮箱连接在一起。在现代600千瓦风力发电机上，转子转速相当慢，大约为19至30转每分钟。轴中有用于液压系统的导管，来激发空气动力闸的运行。 ")]),_c('p',[_vm._v(" 高速轴及其机械闸：高速轴以1500转每分钟运转，并驱动发电机。它装备有紧急机械闸，用于空气动力闸失效时，或风力发电机被维修时。 ")]),_c('p',[_vm._v(" 齿轮箱：齿轮箱左边是低速轴，它可以将高速轴的转速提高至低速轴的50倍。 ")]),_c('p',[_vm._v(" 发电机：通常被称为感应电机或异步发电机。在现代风力发电机上，最大电力输出通常为500至1500千瓦。 ")]),_c('p',[_vm._v(" 偏航装置：借助电动机转动机舱，以使转子正对着风。偏航装置由电子控制器操作，电子控制器可以通过风向标来感觉风向。显示了风力发电机偏航。通常，在风改变其方向时，风力发电机一次只会偏转几度。 ")]),_c('p',[_vm._v(" 电子控制器：包含一台不断监控风力发电机状态的计算机，并控制偏航装置。为防止任何故障（即齿轮箱或发电机的过热），该控制器可以自动停止风力发电机的转动，并通过电话调制解调器来呼叫风力发电机操作员。 ")]),_c('p',[_vm._v("液压系统：用于重置风力发电机的空气动力闸。")]),_c('p',[_vm._v(" 冷却元件：包含一个风扇，用于冷却发电机。此外，它包含一个油冷却元件，用于冷却齿轮箱内的油。一些风力发电机具有水冷发电机。 ")]),_c('p',[_vm._v(" 塔：风力发电机塔载有机舱及转子。通常高的塔具有优势，因为离地面越高，风速越大。现代600千瓦风汽轮机的塔高为40至60米。它可以为管状的塔，也可以是格子状的塔。管状的塔对于维修人员更为安全，因为他们可以通过内部的梯子到达塔顶。格状的塔的优点在于它比较便宜。 ")]),_c('p',[_vm._v("风速计及风向标：用于测量风速及风向")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"item",attrs:{"id":"step"}},[_c('div',{staticClass:"line"}),_c('div',{staticClass:"title"},[_vm._v("实验注意事项")]),_c('div',{staticClass:"content"},[_c('div',{staticClass:"main-text"},[_c('p',{staticClass:"info-title"},[_vm._v("推荐运行环境")]),_c('div',{staticClass:"info-text"},[_c('p',[_vm._v(" 系统基于WebGL，全程于Google Chrome上开发，Google Chrome对WebGL的支持程序在所有浏览器中支持度最高，运行该系统时推荐使用Google浏览器87版本及以上。 ")]),_c('p',[_vm._v(" 本系统运用了的大量图形与模型，对计算机显示设备性能有一定的要求，推荐使用高性能的核显或者独立显卡。 ")])]),_c('p',{staticClass:"info-title"},[_vm._v("运行过程中的注意事项")]),_c('div',{staticClass:"info-text"},[_c('p',[_vm._v("虚拟仿真运行过程中请不要进行刷新，一方面进行了刷新已完成的成绩不会保存，另一方面系统加载模型需要大量时间，刷新不会完全加载模型可能会导致模型不能显示。")]),_c('p',[_vm._v("如虚拟仿真出现意外，可退出实验或者退回至上一页面重新进行仿真页面重新开始。")]),_c('p',[_vm._v("在虚拟仿真运行过程中，如页面卡顿，可将场景更换项切换至‘网格场景’，如还是卡顿请更换设备使用上述推荐运行环境设备。")])])])])])}]

